import { createRouter, createWebHistory } from 'vue-router';
import HomeView from '../views/HomeView.vue';
import AboutView from '../views/AboutView.vue';

const routes = [
  { path: '/CoinDS/', component: HomeView }, // ‚úÖ Adjusted for GitHub Pages
  { path: '/CoinDS/about', component: AboutView }, // ‚úÖ Ensure correct path for GitHub Pages
];

const router = createRouter({
  history: createWebHistory(import.meta.env.BASE_URL), // ‚úÖ Correct GitHub Pages path handling
  routes
});

export default router;
