<template>
  <div>
    <h1>üî• Top 100 Crypto Prices üî•</h1>
    <p v-if="errorMessage" class="error">{{ errorMessage }}</p>
    <p v-if="coins.length === 0 && !errorMessage">Loading prices... üîÑ</p>

    <table v-if="coins.length > 0">
      <thead>
        <tr>
          <th>Rank</th>
          <th>Coin</th>
          <th>Price</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="coin in coins" :key="coin.id">
          <td>{{ coin.market_cap_rank }}</td>
          <td>{{ coin.name }} ({{ coin.symbol.toUpperCase() }})</td>
          <td>${{ coin.current_price.toLocaleString() }}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
console.log("üìÑ HomeView.vue loaded!"); // Debugging

export default {
  data() {
    return {
      coins: [],
      errorMessage: "",
    };
  },
  async created() {
    console.log("üîÑ created() function is running..."); // ‚úÖ Check if this logs

    try {
      console.log("üì° Fetching coin data from API...");
      const response = await fetch(
        "https://api.coingecko.com/api/v3/coins/markets?vs_currency=usd&order=market_cap_desc&per_page=100&page=1&sparkline=false"
      );

      if (!response.ok) {
        throw new Error(`HTTP error! Status: ${response.status}`);
      }

      const data = await response.json();
      console.log("‚úÖ Coin data received in Vue:", data);
      this.coins = [...data]; // ‚úÖ Ensure Vue updates reactivity
    } catch (error) {
      console.error("‚ùå Error fetching coins:", error);
      this.errorMessage = "‚ö†Ô∏è Failed to load coin prices. Please try again later.";
    }
  },
};
</script>

<style>
.error {
  color: red;
  font-weight: bold;
}
table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}
th, td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}
th {
  background-color: #4CAF50;
  color: white;
}
</style>
